  background-image: linear-gradient(var(--bg-oxford-blue), var(--bg-oxford-blue)), var(--gradient-2);
  background-clip: padding-box, border-box;
  border-color: transparent;
  transform: translateY(-2px);
  transition: var(--transition-1);
}

.tag-btn img {
 width: 80%;
 margin: auto;
 padding: 3px; 
}

.tag-btn > div{
 
  background: #17203d;
    border-radius: 50%;
    font-size: 3.7rem;
    height: 70px;
    justify-content: center;
    margin-top: 5px;
    width: 70px;
}



/*-----------------------------------*\
  #FEATURED
\*-----------------------------------*/

.feature-bg { display: none; }

.feature-list {
  display: flex;
  gap: 30px;
  
}

.feature-card {
  padding: 20px;
  border-radius: var(--radius-16);
  transition: var(--transition-1);
}

.feature-card:is(:hover, :focus-within) { transform: translateY(-5px); }

.feature-card .card-banner { border-radius: var(--radius-16); }

.feature-card .card-wrapper { margin-block-start: 24px; }
.feature-card img {
  transition: transform 0.3s ease-out; /* Smooth transition for scaling */
  max-width: 100%;
  height: auto;
  display: block;
}

.feature-card img:hover {
  transform: scale(1.05); /* Slightly larger scale on hover */
}

.feature .profile-card :is(.card-title, .card-subtitle) { color: var(--text-slate-gray); }

.feature .profile-card .card-title { font-weight: var(--weight-bold); }

.feature .profile-card .card-subtitle,
.feature .card-wrapper .card-btn { font-size: var(--fontSize-6); }

.card-btn img:hover{
  box-shadow: var(--shadow-1);
}
.feature .card-wrapper .card-btn {
  position: relative;
  color: var(--text-wild-blue-yonder);
  z-index: 1;
}
.github img:hover{
  box-shadow: var(--shadow-2);
}
.feature .card-wrapper .card-btn::before {
  content: "";
  position: absolute;
  top: -13px;
  left: -20px;
  width: 48px;
  height: 48px;
  background-color: var(--bg-oxford-blue-2);
  border-radius: var(--radius-circle);
  z-index: -1;
}

.feature .btn {
  margin-inline: auto;
  margin-block-start: 30px;
}


.card-desc{
  font-size: 14px;
  }
  .card-tag {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 6px 12px;
    border-radius: 8px;
  }
  
  .card-tag .span {
    background: var(--gradient-2); /* Gradient background for the text */
    -webkit-background-clip: text; /* Apply the gradient to the text */
    background-clip: text;
    color: transparent; /* Make the text transparent to reveal the gradient */
    transition: color 0.3s ease-out; /* Smooth transition when revealing the text */
    font-weight: 600;
  }
  
  .card-tag .span:hover {
    color: white; /* Color of the text on hover, revealing the gradient */
  }
  
  .card-tag .span::after {
    content: " | ";
    margin-left: 6px;
    color: white;
  }
  
  /* Add animation keyframes */
@keyframes underlineAnimation {
  0% {
    width: 0;
  }
  100% {
    width: max-content;
  }
}

/* Apply styles to the underline span */
.underline {
  position: absolute;
  bottom: -3px; /* Adjust as needed */
  left: 0;
  height: 2px; /* Adjust as needed */
  background-color: #59c378;

  content: ''; /* Required for the pseudo-element */
  display: inline-block; /* Match width to heading content */
  animation: underlineAnimation 0.7s forwards; /* Apply the animation */
}


.back-top-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  background-color: var(--bg-prussian-blue);
  color: var(--white);
  display: grid;
  place-items: center;
  border-radius: var(--radius-circle);
  border: 3px solid var(--bg-carolina-blue);
  transition: var(--transition-1);
  visibility: hidden;
  opacity: 0;
  z-index: 4;
}

.back-top-btn:is(:hover, :focus-visible) { box-shadow: var(--shadow-2); }

.back-top-btn.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(-10px);
}


.section-top-btn {
  position: absolute;
  top: 50%;
  width: 50px;
  height: 50px;
  background-color: var(--bg-prussian-blue);
  color: var(--white);
  display: grid;
  place-items: center;
  border-radius: var(--radius-circle);
  border: 3px solid var(--bg-carolina-blue);
  transition: var(--transition-1);
  visibility: visible;
  z-index: 4;
}

.section-top-btn:is(:hover, :focus-visible) { box-shadow: var(--shadow-2); }

.section-top-btn:hover {
  visibility: visible;
  opacity: 1;
  
}
.accordian-btn:is(:hover, :focus-visible) { box-shadow: var(--shadow-2); }

.accordian-btn {
  position: fixed;
  /* bottom: 85px; */
  right: 22px;
  width: 42px;
  height: 42px;

  color: var(--white);
  cursor: pointer;
  z-index: 4;
  transform: rotate(180deg);
  background-color: var(--bg-prussian-blue);
  color: var(--white);
  display: grid;
  place-items: center;
  border-radius: var(--radius-circle);
  border: 2px solid var(--bg-carolina-blue);
  transition: var(--transition-1);
}

.accordion {
  border: none;
}
.spin-animation{
  animation: spin 2s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Stop the animation on hover */
.accordian-btn:hover .spin-animation {
  animation-play-state: paused;
}


/*-----------------------------------*\
  #FOOTER
\*-----------------------------------*/

.footer {
  padding-inline: 20px;
  border-radius: var(--radius-48);
  color: var(--text-wild-blue-yonder);
  margin-block-end: 24px;
}

.footer-top {
  display: grid;
  gap: 20px;
}

.footer-brand .footer-text { font-size: var(--fontSize-6); }

.footer-text:not(.address) { margin-block: 20px; }

.footer-brand .footer-list-title { margin-block-end: 2px; }

.footer-list-title {
  color: var(--text-columbia-blue);
  font-size: var(--fontSize-3);
  margin-block-end: 30px;
}

.footer-list ul { columns: 2; }

.footer-link { margin-block-end: 12px; }
.footer .input-wrapper {
  display: flex;
  align-items: center; /* Align items vertically in the center */
  margin-block-end: 24px;
}

.footer .input-field {
  flex: 1; /* Let the input field take up the remaining space */
  padding: 12px;
  padding-inline-start: 20px;
  border-block-end: 1px solid var(--text-wild-blue-yonder);
  font-size: var(--fontSize-6);
}

.footer .input-field::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.5;
}
.footer .input-wrapper icon{
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translateY(-50%); 
  --reacticon-stroke-width: 40px;
}

.footer-bottom {
  border-block-start: 1px solid var(--border-prussian-blue);
  padding-block: 50px;
  padding-bottom: 10px;
}

.copyright {
  text-align: center;
  margin-block-end: 15px;
}

.copyright-link {
  display: inline-block;
  color: var(--text-carolina-blue);
  font-style: italic;
}

.social-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 8px;
  transition: var(--transition-1);
}

.social-link:is(:hover, :focus-visible) {
  color: var(--text-carolina-blue);
  transform: translateY(-2px);
}


/*-----------------------------------*\
  #CONTACT
\*-----------------------------------*/

.contact-card {
 
  padding: 50px 25px;

}

.contact-card .card-subtitle {
  color: var(--orange-soda);
  font-family: var(--fontFamily-noto_sans);
  font-size: var(--fs-4);
  font-weight: var(--fw-600);
}

.contact .section-title {
  text-align: left;
  margin-block-end: 30px;
}

.contact-form { margin-block-end: 50px; }

.contact-input::placeholder { color: inherit; }

textarea.contact-input {
  resize: vertical;
  min-height: 100px;
  height: 180px;
  max-height: 300px;
}


.contact-item {
  display: flex;
  align-items: center;
  gap: 15px;
}

.contact-item:not(:last-child) { margin-block-end: 20px; }

.contact-icon {
  background-color: var(--glaucous);
  font-size: 2.4rem;
  padding: 20px;
  border-radius: 50%;
}

.contact-item-title {
  color: var(--white);
  font-family: var(--ff-barlow);
  font-size: var(--fs-4);
  font-weight: var(--fw-600);
}

